
























import { Component, Prop, Vue } from 'vue-property-decorator';
import { State, Item } from '../store/index';

@Component
export default class CartItem extends Vue 
{
  @Prop({required: true})
  public item!: Item;

  @Prop({required: false, default: false})
  public readonly!: boolean;

  public get Color(): string
  {
    return this.item.Color;
  }

  public get Size(): string
  {
    return this.item.Size;
  }

  public get Amount(): number
  {
    return this.item.Amount;
  }

  public get Cost(): number
  {
    return this.item.Cost;
  }

  public get InStock(): number
  {
    const state = this.$store.state as State;

    return state.StockData.GetAmountInStock(this.Color, this.Size);
  }

  public get NotEnoughInStock(): boolean
  {
    if (this.InStock == -1) return false;

    return this.InStock < this.Amount;
  }

  public get IsIncrementButtonEnabled(): boolean
  {
    if (this.InStock == -1) return true;

    return this.InStock > this.Amount;
  }

  public Increment(): void
  {
    this.$store.commit('AddItemToCart', new Item(this.Color, this.Size, 1));
  }

  public Decrement(): void
  {
    this.$store.commit('AddItemToCart', new Item(this.Color, this.Size, -1));
  }
}
