










































































































import { Component, Vue, Watch } from 'vue-property-decorator';
import { Address, ItemType, OrderingState, OrderItem, OrderRequest, State } from '../store/index';
import Cart from '@/components/Cart.vue'; // @ is an alias to /src

@Component({
  components: {
    Cart,
  },
})
export default class Checkout extends Vue 
{
  private deliver = false;
  private acceptTerms = false;
  private bankTransfer = true;

  private name = "";
  private email = "";
  
  private addressLine1 = "";
  private addressLine2 = "";
  private zipCode = "";
  private city = "";

  constructor()
  {
    super();

    this.$store.commit("Ordering_RESET");
  }

  public get Ordering(): boolean
  {
    const state = this.$store.state as State;

    return state.OrderingState == OrderingState.Ordering;
  }

  public get NotEnoughItemsInStock(): boolean
  {
    const state = this.$store.state as State;

    return state.OrderingState == OrderingState.NotEnoughInStock;
  }

  public get ServerErrorOccured(): boolean
  {
    const state = this.$store.state as State;

    return state.OrderingState == OrderingState.ServerError;
  }

  public get UnexpectedErrorOccured(): boolean
  {
    const state = this.$store.state as State;

    return state.OrderingState == OrderingState.UnexpectedError;
  }

  public get CanBuy(): boolean
  {
    return  this.AllFieldsAreFilled &&
            this.acceptTerms &&
            !this.Ordering &&
            this.CartIsFilledAndEnoughItemsAreInStock;
  }

  public get CartIsFilledAndEnoughItemsAreInStock(): boolean
  {
    const state = this.$store.state as State;
    
    return state.TotalItemCount > 0 && !this.MoreItemsInCartThanInStock;
  }

  public get MoreItemsInCartThanInStock(): boolean
  {
    const state = this.$store.state as State;

    return state.MoreItemsInCartThanInStock;
  }

  public get AllFieldsAreFilled(): boolean
  {
    return  this.NameFieldIsFilled &&
            this.EmailFieldIsFilledAndValid &&
            this.DeliveryFieldsAreFilledAndValid;
  }

  public get DeliveryFieldsAreFilledAndValid(): boolean
  {
    if (this.deliver)
    {
      return  this.AddressLine1FieldIsFilled &&
              this.ZipCodeFieldIsFilledAndValid &&
              this.CityFieldIsFilled;
    }
    else
    {
      return true;
    }
  }

  public get NameFieldIsFilled(): boolean
  {
    return  this.name.length > 0;
  }

  public get EmailFieldIsFilledAndValid(): boolean
  {
    return  this.email.length > 0 && this.EmailIsValid;
  }

  public get AddressLine1FieldIsFilled(): boolean
  {
    return  this.addressLine1.length > 0;
  }

  public get ZipCodeFieldIsFilledAndValid(): boolean
  {
    return  this.zipCode.length > 0 &&
            /[0-9]+/.test(this.zipCode);
  }

  public get CityFieldIsFilled(): boolean
  {
    return  this.city.length > 0;
  }

  public Buy()
  {
    const orderRequest = new OrderRequest();
    orderRequest.name = this.name;
    orderRequest.email = this.email;

    if (this.deliver)
    {
      orderRequest.shippingAddress = new Address();
      orderRequest.shippingAddress.addressLine1 = this.addressLine1;
      orderRequest.shippingAddress.addressLine2 = this.addressLine2;
      orderRequest.shippingAddress.postalCode = parseInt(this.zipCode);
      orderRequest.shippingAddress.cityName = this.city;
      orderRequest.payCash = false;
    }
    else
    {
      orderRequest.shippingAddress = null;
      orderRequest.payCash = !this.bankTransfer;
    }

    const state = this.$store.state as State;
    orderRequest.items = state.Items.map(item => {
      const orderItem = new OrderItem();
      orderItem.amount = item.Amount;
      orderItem.type = new ItemType();
      orderItem.type.color = item.Color;
      orderItem.type.size = item.Size;

      return orderItem;
    });

    this.$store.dispatch('Order', orderRequest);
  }

  @Watch('deliver')
  onPropertyChanged(newValue: boolean, oldValue: boolean): void
  {
    if (newValue)
    {
      this.bankTransfer = true;
    }
  }

  private get EmailIsValid(): boolean 
  {
    return /\S+@\S+\.\S+/.test(this.email);
  }
}
