





































































































import { Component, Vue } from 'vue-property-decorator';
import Cart from '@/components/Cart.vue'; // @ is an alias to /src
import { Item, State } from '../store/index';

@Component({
  components: {
    Cart,
  },
})
export default class Shop extends Vue {
  public size = 'M' ;
  public color = 'BLACK';
  public colorName = "colors.BLACK";
  public amount = 1;
  
  public get CartHasItems(): boolean
  {
    const state = this.$store.state as State;
    
    return state.TotalItemCount > 0;
  }

  public get noneInStock(): boolean
  {
    return this.inStock == 0;
  }

  public get notEnoughInStock(): boolean
  {
    if (this.inStock == -1)
    {
      return false;
    }
    else
    {
      return this.inStock < this.amount;
    }
  }

  public get inStock(): number
  {
    const state = this.$store.state as State;

    return state.StockData.GetAmountInStock(this.color, this.size);
  }

  public SetSelectedSize(size: string): void
  {
    switch (size.toUpperCase())
    {
      case 'XS': 
        this.size = 'XS';
        break;
      case 'S': 
        this.size = 'S';
        break;
      case 'L': 
        this.size = 'L';
        break;
      case 'XL': 
        this.size = 'XL';
        break;
      case 'XXL': 
        this.size = 'XXL';
        break;
      case 'XXXL': 
        this.size = 'XXXL';
        break;
      default: 
        this.size = 'M';
        break;
    }
  }

  public SetSelectedColor(color: string): void
  {
    color = color.toUpperCase();

    switch (color)
    {
      case 'GREY': 
      case 'BLUE':
      case 'RED':
        this.color = color;
        this.colorName = "colors." + color;
        break;
      default:
        this.color = 'BLACK';
        this.colorName = "colors.BLACK";
        break;
    }
  }

  public AddToCart(): void
  {
    this.$store.commit('AddItemToCart', new Item(this.color, this.size, this.amount));
  }
}
