














































































































import { Order, OrderItem, State } from '@/store';
import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {
  },
})
export default class OrderDetails extends Vue 
{
  private get order(): Order | null
  {
    const state = this.$store.state as State;

    if (state.Order == null)
    {
      this.$router.push({ name: "Not Found" });
    }

    return state.Order;
  }
  
  public get DataLoaded(): boolean
  {
    return this.order !== null;
  }

  public get OrderId(): string
  {
    return this.order?.id ?? "null";
  }

  public get Name(): string
  {
    return this.order?.name ?? "null";
  }

  public get EMail(): string
  {
    return this.order?.email ?? "null";
  }

  public get Deliver(): boolean
  {
    if (this.DataLoaded)
    {
      return this.order?.shippingAddress !== null;
    } 
    else
    {
      return false;
    }
  }

  public get AddressLine1(): string
  {
    return this.order?.shippingAddress?.addressLine1 ?? "null";
  }

  public get AddressLine2(): string
  {
    return this.order?.shippingAddress?.addressLine2 ?? "";
  }

  public get ZipCode(): string
  {
    return this.order?.shippingAddress?.postalCode.toString().padStart(5, '0') ?? "null";
  }

  public get City(): string
  {
    return this.order?.shippingAddress?.cityName ?? "null";
  }

  public get BankTransfer(): boolean
  {
    return !(this.order?.payCash ?? false);
  }

  public get Status(): string
  {
    const status = this.order?.state ?? "null";

    if (status == "created")
    {
      return this.BankTransfer ? "orderDetails.status.created.bankTransfer" : "orderDetails.status.created.selfPickup";
    }
    else
    {
      return status;
    }
  }

  public get TotalCount(): number
  {
    let totalCount = 0;

    this.Items.forEach(item => {
      totalCount += item.amount;
    });

    return totalCount;
  }

  public get TotalCost(): number
  {
    return this.TotalCount * 25;
  }

  public get Items(): OrderItem[]
  {
    return this.order?.items ?? [];
  }
}
