













import { Component, Prop, Vue } from 'vue-property-decorator';
import { State, Item } from '../store/index';
import CartItem from '@/components/CartItem.vue';

@Component({
  components: {
    CartItem,
  },
})
export default class Cart extends Vue 
{
  @Prop({required: false, default: false})
  public readonly!: boolean;

  public get Items(): Item[] 
  {
    const state = this.$store.state as State;
    
    return state.Items;
  }
  
  public get TotalCount(): number 
  {
    const state = this.$store.state as State;
    return state.TotalItemCount;
  }

  public get TotalCost(): number 
  {
    const state = this.$store.state as State;
    return state.TotalItemCost;
  }

  public Increment(color: string, size: string): void
  {
    this.$store.commit('AddItemToCart', new Item(color, size, 1));
  }

  public Decrement(color: string, size: string): void
  {
    this.$store.commit('AddItemToCart', new Item(color, size, -1));
  }
}
