import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import Shop from '../views/Shop.vue'
import Checkout from '../views/Checkout.vue'
import OrderDetails from '../views/OrderDetails.vue'
import PrivacyPolicy from '../views/PrivacyPolicy.vue'
import Imprint from '../views/Imprint.vue'
import AGB from '../views/AGB.vue'
import CancellationPolicy from '../views/CancellationPolicy.vue'
import NotFound from '../views/NotFound.vue'
import { i18n } from '../main';

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'Shop',
    component: Shop
  },
  {
    path: '/checkout',
    name: 'Checkout',
    component: Checkout
  },
  {
    path: '/orderdetails',
    name: 'OrderDetails',
    component: OrderDetails
  },
  {
    path: '/privacy',
    name: 'Datenschutzerklrung',
    component: PrivacyPolicy
  },
  {
    path: '/imprint',
    name: 'Impressum',
    component: Imprint
  },
  {
    path: '/agb',
    name: 'AGB',
    component: AGB
  },
  {
    path: '/cancellation-policy',
    name: 'Cancellation Policy',
    component: CancellationPolicy
  },
  {
    path: '*',
    name: 'Not Found',
    component: NotFound
  }
]

const router = new VueRouter({
  routes
});

router.afterEach((to, from) => {
  let languageValue = to.query.lang;
  
  if (typeof(languageValue) ===  'string')
  {
    languageValue = (languageValue as string).trim().toLowerCase();

    if (languageValue == '')
    {
      return;
    }
    else if (languageValue == 'de')
    {
      i18n.locale = 'de';
    }
    else
    {
      i18n.locale = 'en';
    }  
  }
  

  
});

export default router


