import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios';
import router from '../router/index';


const Created = 201;
const NoContent = 204;

Axios.defaults.baseURL = "https://sturashopbackend.azurewebsites.net"

Vue.use(Vuex)

export abstract class StockData
{
  public readonly Created: Date;

  constructor(created: Date)
  {
    this.Created = created;
  }

  public abstract GetAmountInStock(color: string, size: string): number;
}

class NullStockData extends StockData
{
  public constructor()
  {
    super(new Date());
  }

  public GetAmountInStock(color: string, size: string): number
  {
    return -1;
  }
}

class ImmutableStockData extends StockData
{
  private readonly _stockData: Map<string,Map<string, number>>;

  constructor(stockData: Map<string,Map<string, number>>, created: Date | null = null)
  {
    super(created === null ? new Date() : created);

    this._stockData = stockData;
  }

  public GetAmountInStock(color: string, size: string): number
  {
    return this._stockData.get(color)?.get(size) ?? 0;
  }
}

export class Address
{
  public addressLine1 = "null";
  public addressLine2 = "";
  public postalCode = 0;
  public cityName = "null";
}

export class ItemType
{
  public color = "null";
  public size = "null";
}

export class OrderItem
{
  public type = new ItemType();
  public amount = 0;

  public get UniqueId(): string
  {
    return `${this.type.color}${this.type.size}${this.amount}`;
  }

  public get Cost(): number
  {
    return this.amount * 25;
  }
}

export class OrderRequest
{
  public name = "null";
  public email = "null";
  public shippingAddress: Address | null = null;
  public payCash = false;
  public items: OrderItem[] = [];
}

export class Order
{
  public id = "null";
  public name = "null";
  public email = "null";
  public shippingAddress: Address | null = null;
  public payCash = false;
  public state = "created";
  public items: OrderItem[] = [];
}

export enum OrderingState
{
  None,
  Ordering,
  NotEnoughInStock,
  ServerError,
  UnexpectedError
} 

export class State 
{
  public MoreItemsInCartThanInStock = false;
  public Items: Item[] = [];
  public TotalItemCount = 0;
  public TotalItemCost = 0;
  public OrderingState = OrderingState.None;
  public Order: Order | null = null;
  public StockData: StockData = new NullStockData(); 
}

export class Item 
{
  private readonly _color: string;
  private readonly _size: string;

  public constructor(
    color: string,
    size: string,
    amount: number)
  {
    this._color = color;
    this._size = size;
    this.Amount = amount;
  }

  public get Id(): string
  {
    return this.Color + this.Size;
  }

  public get Color(): string
  {
    return this._color;
  }

  public get Size(): string
  {
    return this._size;
  }

  public Equals(item: Item): boolean
  {
    return this.Color == item.Color && this.Size == item.Size;
  }

  public Amount: number;

  public get Cost(): number
  {
    return this.Amount * 25;
  }
}

export default new Vuex.Store({
  state: new State(),
  mutations: {
    AddItemToCart(state: State, item: Item)
    {
      if (item.Amount == 0)
      {
        return;
      }

      state.TotalItemCount += item.Amount;
      state.TotalItemCost += item.Cost;

      let addItem = true;

      for (let i = 0; i < state.Items.length; ++i)
      {
        const itemInCart = state.Items[i];

        if (itemInCart.Equals(item))
        {
          itemInCart.Amount += item.Amount;

          if (itemInCart.Amount == 0)
          {
            state.Items.splice(i, 1);
          }

          addItem = false;
          break;
        }
      }

      for (let i = 0; i < state.Items.length; ++i)
      {
        const itemInCart = state.Items[i];
        const inStock = state.StockData.GetAmountInStock(itemInCart.Color, itemInCart.Size);
        
        if (inStock != -1 && itemInCart.Amount > inStock)
        {
          state.MoreItemsInCartThanInStock = true;
          break;
        }
      }

      if (addItem)
      {
        state.Items.push(item);
      }
    },
    // eslint-disable-next-line @typescript-eslint/camelcase
    Ordering_RESET(state: State)
    {
      state.OrderingState = OrderingState.None;
    },
    // eslint-disable-next-line @typescript-eslint/camelcase
    Ordering_START(state: State)
    {
      state.Order = null;
      state.OrderingState = OrderingState.Ordering;
    },
    // eslint-disable-next-line @typescript-eslint/camelcase
    Ordering_SUCCESS(state: State, order: Order)
    {
      state.Order = order;
      state.OrderingState = OrderingState.None;

      state.Items = [];
      state.TotalItemCost = 0;
      state.TotalItemCount = 0;
    },
    // eslint-disable-next-line @typescript-eslint/camelcase
    Ordering_FAILURE_NotEnoughInStock(state: State)
    {
      state.Order = null;
      state.OrderingState = OrderingState.NotEnoughInStock;
    },
    // eslint-disable-next-line @typescript-eslint/camelcase
    Ordering_FAILURE_ServerError(state: State)
    {
      state.Order = null;
      state.OrderingState = OrderingState.ServerError;
    },
    // eslint-disable-next-line @typescript-eslint/camelcase
    Ordering_FAILURE_UnexpectedError(state: State)
    {
      state.Order = null;
      state.OrderingState = OrderingState.UnexpectedError;
    },
    SetStockData(state: State, newStockData: StockData)
    {
      if (newStockData.Created <= state.StockData.Created) return;

      let moreItemsInCartThanInStock = false;

      for (let i = 0; i < state.Items.length; ++i)
      {
        const itemInCart = state.Items[i];
        const inStock = newStockData.GetAmountInStock(itemInCart.Color, itemInCart.Size);
        
        if (inStock != -1 && itemInCart.Amount > inStock)
        {
          moreItemsInCartThanInStock = true;
          break;
        }
      }
      
      state.StockData = newStockData;
      state.MoreItemsInCartThanInStock = moreItemsInCartThanInStock;
    }
  },
  actions: {
    Order(context, orderRequest: OrderRequest)
    {
      context.commit("Ordering_START");

      Axios.post("/orders", orderRequest)
        .then(response => {
          try 
          {
            if (response.status == Created)
            {
              const order = response.data as Order;

              context.commit("Ordering_SUCCESS", order);
              router.push({ name: "OrderDetails" });
            }
            else if (response.status == NoContent)
            {
              context.commit("Ordering_FAILURE_NotEnoughInStock");
              context.dispatch('RefreshStockData');
            }
          } 
          catch (error) 
          {
            context.commit("Ordering_FAILURE_UnexpectedError");  
          }
        })
        .catch(error => {        
          context.commit("Ordering_FAILURE_UnexpectedError");
        });
    },
    RefreshStockData(context)
    {
      console.info("Requesting stock data");

      Axios.get('/stock')
        .then(response => {
          try
          {
            if (response.status == 200)
            {
              const responseDate = new Date();
              const newStockData = new Map<string, Map<string, number>>();

              for (const color in response.data) 
              {  
                const colorStockData = new Map<string, number>();

                for (const size in response.data[color]) 
                {  
                  const amount: number = response.data[color][size];

                  if (amount < 0)
                  {
                    continue;
                  }

                  colorStockData.set(size, response.data[color][size]);
                } 

                newStockData.set(color, colorStockData);
              }  

              context.commit('SetStockData', new ImmutableStockData(newStockData, responseDate));
            }
          }
          catch
          {
            console.warn("Failed to parse stock data response!");
          }
        })
        .catch(error => {
          console.warn("Requesting stock data failed!");
        });
    }
  },
  modules: {

  }
})
